import React, { Component } from 'react';
import PropTypes from 'prop-types';

import {NavLink} from 'react-router-dom';

export default class HiddenBlockProfileAndFavorite extends Component {

    render() {
        return (
            <div className={`hidden-panel__profile ${this.props.active ? "hidden-panel__profile_visible" : ""}`}>
                <a>Личный кабинет</a>
                <NavLink to="/favorite"><i className="fa fa-heart-o" aria-hidden="true"/>Избранное</NavLink>
                <a>Выйти</a>
            </div>
        );
    }
}

HiddenBlockProfileAndFavorite.PropTypes = {
    active:PropTypes.bool
};


