import React, { Component } from 'react';
import { BubbleLoader } from 'react-css-loaders';

import {Link} from 'react-router-dom';


export default class OrderProductItem extends Component {

    constructor(props) {
        super(props);

        this.onClickCounter = this.onClickCounter.bind(this);
    }

    componentWillReceiveProps(newProps) {
        this.setState({
            amount:newProps.amount
        });
    }

    onClickCounter(isIncrement) {
        let newCount = this.props.amount + (isIncrement ? 1 : -1);

        if(newCount > 0 && this.props.onChangeCount) {
            this.props.onChangeCount(this.props.id, this.props.size, newCount);
        }
    }

    render () {

        return (
            <div className="basket-item" key={`${this.props.id}-${this.props.size}`}>
                <div className="basket-item__pic"><img src={this.props.images[0]} alt={this.props.title}/></div>
                <div className="basket-item__product">
                    <div className="basket-item__product-name"><Link to={`/product/${this.props.id}`} >{this.props.title}</Link></div>
                    <div className="basket-item__product-features">
                        <div className="basket-item__size">Размер: <span>{this.props.size}</span></div>
                        <div className="basket-item__producer">Производитель: <span>{this.props.brand}</span></div>
                        <div className="basket-item__color">Цвет: <span>{this.props.color}</span></div>
                    </div>
                </div>
                <div className="basket-item__quantity">
                    <div className="basket-item__quantity-change basket-item-list__quantity-change_minus" onClick={() => {this.onClickCounter(false)}} >-</div>
                    {this.props.amount}
                    <div className="basket-item__quantity-change basket-item-list__quantity-change_plus" onClick={() => {this.onClickCounter(true)}} >+</div>
                </div>
                <div className="basket-item__price">{this.props.price} <i className="fa fa-rub" aria-hidden="true"/></div>
            </div>
        );
    }
}




