import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class FilterAbstractCheckboxList extends Component {

    constructor(props) {
        super(props);

        this.state = {
            active: this.props.filter[this.key] || []
        };

        this.onClickCheckbox = this.onClickCheckbox.bind(this);
    }

    componentWillReceiveProps(newProps) {
        this.setState({
            active:newProps.filter[this.key] || []
        })
    }

    onClickCheckbox(size) {

        let items = this.state.active || [];
        let index = items.indexOf(size);

        if(index !== -1) {
            items.splice(index,1);
        } else {
            items.push(size);
        }

        let filter = {
            key:this.key,
            value:items
        };

        this.props.onChangeFilter([filter]);
    }
}

FilterAbstractCheckboxList.PropTypes = {
    filter:PropTypes.object.isRequired,
    onChangeFilter:PropTypes.func.isRequired
};




