import React, { Component } from 'react';

import FilterDivisionList from './FilterDivisionList';
import FilterAbstractTextList from './FilterAbstractTextList';

import CatalogConfig from '../../../../Config/CatalogConfig';

export default class FilterBrand extends FilterAbstractTextList {

    key = "brand";

    constructor(props) {
        super(props);
    }

    render() {

        return (
            <FilterDivisionList title="Бренд" className="sidebar__catalogue-list">
                {
                    CatalogConfig.filter.brands.map(item => <li className={this.state.active === item ? "filter-item-active" : ""} ><a onClick={() => {this.onChangeActive(item)}} >{item}</a></li>)
                }
            </FilterDivisionList>
        );
    }
}



