import React, { Component } from 'react';

import FilterDivisionList from './FilterDivisionList';
import FilterAbstractTextList from './FilterAbstractTextList';

import CatalogConfig from '../../../../Config/CatalogConfig';

export default class FilterType extends FilterAbstractTextList {

    key = "type";

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <FilterDivisionList title="Каталог" className="sidebar__catalogue-list">
                {
                    CatalogConfig.filter.type.map(item => <li className={this.state.active === item ? "filter-item-active" : ""} key={item}><a onClick={() => {this.onChangeActive(item)}}>{item}</a></li>)
                }
            </FilterDivisionList>
        );
    }
}



