import React, { Component } from 'react';

import FilterDivisionList from './FilterDivisionList';
import FilterAbstractTextList from './FilterAbstractTextList';

import CatalogConfig from '../../../../Config/CatalogConfig';

export default class FilterSeason extends FilterAbstractTextList {

    key = "season";

    constructor(props) {
        super(props);
    }

    render() {

        return (
            <FilterDivisionList title="Сезон" className="sidebar__season">
                {
                    CatalogConfig.filter.seasons.map((item) => <li className={this.state.active === item ? "filter-item-active" : ""} ><a onClick={() => {this.onChangeActive(item)}} >{item}</a></li>)
                }
            </FilterDivisionList>
        );
    }
}



