import React, { Component } from 'react';
import {HashRouter, Switch, Router, Route, BrowserRouter} from 'react-router-dom';

import './css/style.css';
import './css/normalize.css';
import './css/style-catalogue.css';
import './css/font-awesome.min.css';
import './css/style-favorite.css';
import './css/style-order.css';
import  './css/style-product-card.css';

import Header from './Components/Shared/Header/Header';
import IndexPage from './Components/Pages/IndexPage/IndexPage'
import CardProductPage from './Components/Pages/CardProductPage/CardProductPage';
import CatalogPage from './Components/Pages/CatalogPage/CatalogPage';
import FavoritePage from './Components/Pages/FavoritePage/FavoritePage';
import OrderPage from './Components/Pages/OrderPage/OrderPage';
import Page404 from './Components/Pages/Page404';
import Footer from "./Components/Shared/Footer/Footer";

class App extends Component {
  render() {
    return (
        <HashRouter basename="/">
            <div className="container">
                <Header/>

                <Switch>
                    <Route path="/" exact component={IndexPage} />
                    <Route path="/product/:id" component={CardProductPage} />
                    <Route path="/catalog/:categoryId?/:filterKey?/:filterValue?" component={CatalogPage} />
                    <Route path="/favorite" component={FavoritePage} />
                    <Route path="/order" component={OrderPage} />
                    <Route path="/" component={Page404} />
                </Switch>

              <Footer/>
            </div>
        </HashRouter>
    );
  }
}

export default App;



