import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class FilterAbstractTextList extends Component {

    constructor(props) {
        super(props);

        this.state = {
            active:this.props.filter[this.key]
        };

        this.onChangeActive = this.onChangeActive.bind(this);
    }

    componentWillReceiveProps(newProps) {
        this.setState({
            active:newProps.filter[this.key]
        })
    }

    onChangeActive(newActive) {

        let filter;

        if(newActive !== this.state.active) {
            filter = {
                key:this.key,
                value: newActive
            };
        } else {
            filter = {
                key:this.key,
                value: null
            }
        }

        this.props.onChangeFilter([filter]);
    }
}

FilterAbstractTextList.PropTypes = {
    filter:PropTypes.object.isRequired,
    onChangeFilter:PropTypes.func.isRequired
};


