import React, { Component } from 'react';
import $ from 'jquery';

import TextInput from './TextInput';
import OrderRadioGroup from './OrderRadioGroup';

import shopConfig from '../../../../../Config/ShopConfig';

export default class OrderFormInfo extends Component {

    $orderInfo;

    onConfirmOrder(event) {

        if(this.$orderInfo && this.$orderInfo.length !== 0 && this.$orderInfo[0].checkValidity()) {

            const orderInfo = this.getOrderInfo();

            if(this.props.orderRegistration)
                this.props.orderRegistration(orderInfo);

        } else {
            alert("Все поля обязательны для заполнения!!!")
        }

        event.preventDefault();
    }

    getOrderInfo() {

        const name = this.$orderInfo.find('input[name="name"]').val();
        const phone = this.$orderInfo.find('input[name="phone"]').val();
        const address = this.$orderInfo.find('input[name="address"]').val();
        const email = this.$orderInfo.find('input[name="email"]').val();

        const paymentType = this.$orderInfo.find('input[name="paid"]').val();

        return {name, phone, email, address, paymentType};
    }

    render () {

        return (
            <div className="order-process__confirmed">
                <form action="orderInfo" ref={ref => this.$orderInfo = $(ref)} >
                    <div className="order-process__delivery">
                        <h3 className="h3">кому и куда доставить?</h3>
                        <div className="order-process__delivery-form">

                            <TextInput title="Имя" name="name" placeholder="Представьтесь, пожалуйста"/>
                            <TextInput title="Телефон" name="phone" type="tel" placeholder="Номер в любом формате"/>
                            <TextInput title="Email" name="email" tyoe="email" placeholder="Ваш email"/>
                            <TextInput title="Адрес" name="address" placeholder="Ваша покупка будет доставлена по этому адресу"/>

                        </div>
                        <p>Все поля обязательны для заполнения. Наш оператор свяжется с вами для уточнения деталей заказа.</p>
                    </div>
                    <OrderRadioGroup title="хотите оплатить онлайн или курьеру при получении?" name="paid" items={shopConfig.variantsPaid} />
                    <button className="order-process__form-submit order-process__form-submit_click" onClick={this.onConfirmOrder.bind(this)} >Подтвердить заказ</button>
                </form>
            </div>
        );
    }
}




