import React, { Component } from 'react';
import { BubbleLoader } from 'react-css-loaders';

import OrderRegistration from './OrderRegistration/OrderRegistration';
import OrderReserved from './OrderReserved/OrderReserved';

export default class OrderPage extends Component {

    constructor(props) {
        super(props);

        this.state = {
            orderStatus:"registration",
            error:null,
            orderInfo:null
        };

        this.orderChangeStatus = this.orderChangeStatus.bind(this);
    }

    orderChangeStatus (error, orderInfo, sending) {

        if(sending) {

            this.setState({
                orderStatus:"sending",
                error:null
            })

        } else if(!error && orderInfo) {

            this.setState({
                orderStatus:"reserved",
                orderInfo:orderInfo,
                error:null
            })

        } else {

            this.setState({
                orderStatus:"reserved",
                orderInfo:null,
                error:error
            })
        }
    }

    render () {

        if(this.state.orderStatus === "registration") {
            return (
                <OrderRegistration orderChangeStatus={this.orderChangeStatus} />
            );
        }

        if(this.state.orderStatus === "reserved" && this.state.orderInfo) {
            return (
                <OrderReserved {...this.state.orderInfo}/>
            );
        }

        if(this.state.orderStatus === "sending") {
            return (
                <div className="loader">
                    <BubbleLoader/>
                </div>
            );
        }

        if(this.state.orderStatus === "error"  || this.state.error) {
            return (<div>{this.state.error.message ? this.state.error.message : "Произошла ошибка"}</div>);
        }
    }
}




