import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Sizes from './Sizes';
import Favorite from './FavoriteProductInfo';
import Price from './Price';

import ProductCardConfig from '../../../../../Config/ProductCardConfig';
import CartHelper from '../../../../../Classes/CartHelper';


export default class ProductInfo extends Component {

    constructor(props) {
        super(props);

        this.state = {
            size:null,
            amount:1,
            cart:"check"
        };

        this.onChangeSize = this.onChangeSize.bind(this);
        this.onChangeCount = this.onChangeCount.bind(this);
        this.toggleCart = this.toggleCart.bind(this);
        this.setStateCart = this.setStateCart.bind(this);
        this.updateCatrState = this.updateCatrState.bind(this);

        CartHelper.setListener("card", this.updateCatrState);
    }

    componentWillUpdate(newProps, newState) {
        if(newState.size !== this.state.size) {
            this.updateCatrState(newState.size)
        }
    }

    componentWillUnmount() {
        CartHelper.deleteListener("card");
    }

    updateCatrState(size) {
        size = size || this.state.size;
        CartHelper.checkProduct(this.props.productInfo.id, size, this.setStateCart);
    }

    setStateCart(error, isInCart) {

        if(!error) {

            if(isInCart) {

                this.setState({
                    cart:"inCart"
                })
            } else {

                this.setState({
                    cart:"notInCart"
                })
            }
        } else {

            if(this.state.cart !== "error") {

                this.setState({
                    cart:"error"
                })
            }
        }
    }

    onChangeSize(size) {
        this.setState({
            size
        })
    }

    onChangeCount(amount) {
        this.setState({
            amount
        })
    }

    toggleCart() {

        if(this.state.cart === "inCart") {

            CartHelper.deleteProductFromCart(this.props.productInfo.id, this.state.size)
                .then(() => {
                    this.setState({
                        cart:"notInCart"
                    });
                })
                .catch(() => {
                    this.setState({
                        cart:"error"
                    });
                });

            this.setState({
                cart:"updateCart"
            });

        } else if(this.state.cart === "notInCart") {

            CartHelper.addProductInCart(this.props.productInfo.id, this.state.size, this.state.amount)
                .then(() => {
                    this.setState({
                        cart:"inCart"
                    });
                })
                .catch(() => {
                    this.setState({
                        cart:"error"
                    });
                });

            this.setState({
                cart:"updateCart"
            });
        }
    }

    getNameButtonInCart() {

        switch (this.state.cart) {

            case "inCart":
                return "Убрать из корзины";
            case "notInCart":
                return "В корзину";
            case "updateCart":
                return "Добавляем в корзину";
            case "error":
                return "Ошибка";
            case "check":
                return "Проверяем корзину";
            default:
                return "Операция недоступна";
        }
    }

    render() {
        return (
            <div className="main-screen__product-info">
                <div className="product-info-title"><h2>{this.props.productInfo.title}</h2><div className="in-stock">В наличии</div></div>
                <div className="product-features">
                    <table className="features-table">

                        {
                            ProductCardConfig.productInfo.map((item) => {
                                if(this.props.productInfo[item.key]) {
                                    return (
                                        <tr key={item.key}>
                                            <td className="left-col">{item.name}:</td>
                                            <td className="right-col">{this.props.productInfo[item.key]}</td>
                                        </tr>
                                    );
                                }
                            })
                        }

                    </table>
                </div>
                <div className="product-card__shared-button-container">
                    <Sizes sizes={this.props.productInfo.sizes} onChangeSize={this.onChangeSize}/>
                    <Favorite id={this.props.productInfo.id}/>
                    <div className="product-card__price-container-and-basket-button">
                        <Price price={this.props.productInfo.price} onChangeCount={this.onChangeCount}/>
                        <button className="in-basket in-basket-click" onClick={this.toggleCart}>{this.getNameButtonInCart()}</button>
                    </div>
                </div>
            </div>
        );
    }
}

ProductInfo.PropTypes = {
    id:PropTypes.string.isRequired,
    title:PropTypes.string.isRequired,
    brand:PropTypes.string,
    price:PropTypes.string.isRequired
};



