import React, { Component } from 'react';
import PropTypes from 'prop-types';

import {Link} from 'react-router-dom';

export default class CartItem extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="product-list__item" key={this.props.id + this.props.size}>
                <Link to={`/product/${this.props.id}`} className="product-list__pic"><img src={this.props.images[0]} alt="product"/></Link><Link to={`/product/${this.props.id}`} className="product-list__product" href="#">{this.props.title}, {this.props.brand}</Link>
                <div className="product-list__fill"/>
                <div className="product-list__price">{this.props.price * this.props.amount}<i className="fa fa-rub" aria-hidden="true"/></div>
                <div className="product-list__delete" onClick={() => {this.props.onDeleteItem(this.props.id, this.props.size)}}><i className="fa fa-times" aria-hidden="true"/></div>
            </div>
        );
    }
}



