import React, { Component } from 'react';
import PropTypes from 'prop-types';

import HeaderHiddenSearch from "./HeaderHiddenSearch";

import SearchHelper from '../../../../Classes/SearchHelper';

export default class HeaderButtons extends Component {

    constructor(props) {
        super(props);

        this.state = {
            visibleSearch:SearchHelper.getSearchText()
        };

        this.toggleVisibleSearch = this.toggleVisibleSearch.bind(this);
    }

    toggleVisibleSearch() {

        if(this.state.visibleSearch) {
            SearchHelper.deleteSearch();
        }

        this.setState({
            visibleSearch:!this.state.visibleSearch
        })
    }

    render() {
        return (
            <div className="header-main__profile">
                <div className="header-main__pics">
                    <div className={`header-main__pic header-main__pic_search ${this.state.visibleSearch ? "header-main__pic_search_is-hidden" : ""}`} onClick={this.toggleVisibleSearch} >

                    </div>
                    <div className="header-main__pic_border"/>
                    <div className={`header-main__pic header-main__pic_profile ${this.props.activeBlock === "profile" ? "header-main__pic_profile_menu_is-active" : ""}`} onClick={() => {this.props.onChangeActiveBlock("profile")}}>
                        <div className="header-main__pic_profile_menu"/>
                    </div>
                    <div className="header-main__pic_border"/>
                    <div className={`header-main__pic header-main__pic_basket ${this.props.activeBlock === "basket" ? "header-main__pic_basket_menu_is-active" : ""}`} onClick={() => {this.props.onChangeActiveBlock("basket")}}>
                        <div className="header-main__pic_basket_full">1</div>
                        <div className="header-main__pic_basket_menu"/>
                    </div>
                </div>
                <HeaderHiddenSearch active={this.state.visibleSearch} />
            </div>
        );
    }
}

HeaderButtons.PropTypes = {
    onChangeActiveBlock:PropTypes.func.isRequired
};


