import React, { Component } from 'react';
import PropTyps from 'prop-types';

export default class TextInput extends Component {

    constructor(props) {
        super(props);
    }

    render () {

        return (
            <label className="order-process__delivery-label">
                <div className="order-process__delivery-text">{this.props.title}</div>
                <input className="order-process__delivery-input" required name={this.props.name} type={this.props.type} placeholder={this.props.placeholder}/>
            </label>
        );
    }
}

TextInput.PropTypes = {
    title:PropTyps.string,
    name:PropTyps.string.isRequired,
    placeholder:PropTyps.string,
    type:PropTyps.string
};

TextInput.defaultProps = {
    placeholder:"",
    title:"",
    type:"text"
};


