import React, { Component } from 'react';

import $ from 'jquery';

import FilterType from './FilterType';
import FilterPrice from './FilterPrice';
import FilterColor from './FilterColor';
import FilterSize from './FilterSize';
import FilterHeelSize from './FilterHeelSize';
import FilterReason from './FilterReason';
import FilterSeason from './FilterSeason';
import FilterBrand from './FilterBrand';
import FilterDiscount from './FilterDiscount';

import CatalogConfig from '../../../../Config/CatalogConfig';

export default class Filter extends Component {

    constructor(props) {
        super(props);

        this.state = {
            filter:this.props.filter
        };

        this.onChangeFilter = this.onChangeFilter.bind(this);
        this.clearFilter = this.clearFilter.bind(this);
    }

    componentWillReceiveProps(newProps) {
        debugger;
        this.state = {
            filter:newProps.filter
        };
    }

    onChangeFilter(changeArray) {

        let filter = this.state.filter;

        changeArray.map((item) => {
            filter[item.key] = item.value
        });

        this.props.onChangeFilter(filter);
    }

    clearFilter() {

        let filter = CatalogConfig.defaultFilter;
        let categoryId = this.state.filter.categoryId;

        if(categoryId) {
            filter["categoryId"] = categoryId;
        }

        this.props.onChangeFilter(filter);
    }

    render() {

        return (
            <section className="sidebar">

                <FilterType onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>
                <FilterPrice onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>
                <FilterColor onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>
                <FilterSize onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>
                <FilterHeelSize onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>
                <FilterReason onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>
                <FilterSeason onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>
                <FilterBrand onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>
                <FilterDiscount onChangeFilter={this.onChangeFilter} filter={this.state.filter}/>

                <section className="sidebar__division">
                  <div className="drop-down">
                    <a onClick={this.clearFilter} ><span className="drop-down-icon"/>Сбросить</a>
                  </div>
                </section>

          </section> 
        );
    }
}



