import React, { Component } from 'react';

import FilterDivisionList from './FilterDivisionList';
import FilterAbstractTextList from './FilterAbstractTextList';

import CatalogConfig from '../../../../Config/CatalogConfig';

export default class FilterReason extends FilterAbstractTextList {

    key = "reason";

    constructor(props) {
        super(props);
    }

    render() {

        return (
            <FilterDivisionList title="Повод" className="sidebar__occasion">
                {
                    CatalogConfig.filter.reasons.map((item) => <li className={this.state.active === item ? "filter-item-active" : ""} ><a onClick={() => {this.onChangeActive(item)}} >{item}</a></li>)
                }
            </FilterDivisionList>
        );
    }
}



