import React, { Component } from 'react';
import $ from 'jquery';

export default class FilterDivisionList extends Component {

    $openElement;

    constructor(props) {
        super(props);

        this.state = {
            open:this.props.isDefaultOpen || true
        };

        this.toggleBlock = this.toggleBlock.bind(this);
    }

   toggleBlock(event) {
        debugger;
        if(this.state.open) {
            $(event.currentTarget).parent().find('#openingElement').hide(400);
        } else {
            $(event.currentTarget).parent().find('#openingElement').show(400);
        }

        this.setState({
            open:!this.state.open
        });
    }

    render() {

        return (
            <section className="sidebar__division">
                <div className={this.props.className}>
                    <div className="sidebar__division-title" onClick={this.toggleBlock} >
                        <h3>{this.props.title}</h3><div className={this.state.open ? "opener-down" : "opener-up"}/>
                    </div>
                    <ul id="openingElement" className="sidebar__division__values-list">
                        { this.props.children }
                    </ul>
                </div>
            </section>
        );
    }
}



