import React, { Component } from 'react';
import {Link} from 'react-router-dom';

import OrderReservedRow from './OrderReservedRow';

import shopConfig from '../../../../Config/ShopConfig';

export default class OrderReserved extends Component {

    render () {

        return (
            <div className="wrapper order-wrapper">
                <div className="site-path">
                    <ul className="site-path__items">
                        <li className="site-path__item"><a href="index.html">Главная</a></li>
                        <li className="site-path__item"><a href="#">Корзина</a></li>
                        <li className="site-path__item"><a href="order.html">Оформление заказа</a></li>
                        <li className="site-path__item"><a href="#">Заказ принят</a></li>
                    </ul>
                </div>
                <section className="order-done">
                    <h2 className="order-done__title order-process__title">Заказ принят, спасибо!</h2>
                    <div className="order-done__information order-info">

                        <OrderReservedRow title="Сумма заказа:">
                            { this.props.price }<i className="fa fa-rub" aria-hidden="true"/>
                        </OrderReservedRow>

                        <OrderReservedRow title="Способ оплаты:">
                            { shopConfig.variantsPaid.filter(item => item.value === this.props.paymentType)[0].name }
                        </OrderReservedRow>

                        <OrderReservedRow title="Имя клиента:">
                            { this.props.name }
                        </OrderReservedRow>

                        <OrderReservedRow title="Адрес доставки:">
                            { this.props.address }
                        </OrderReservedRow>

                        <OrderReservedRow title="Телефон:">
                            { this.props.phone }
                        </OrderReservedRow>
                    </div>
                    <p className="order-done__notice">Данные о заказе отправлены на адрес <span>{this.props.email}</span></p>
                    <Link to="/catalog/0" className="order-done__continue">продолжить покупки</Link>
                </section>
            </div>
        );
    }
}




