import React, { Component } from 'react';

import FilterDivisionList from './FilterDivisionList';
import FilterAbstractTextList from './FilterAbstractTextList';

import CatalogConfig from '../../../../Config/CatalogConfig';

export default class FilterColor extends FilterAbstractTextList {

    constructor(props) {
        super(props);
    }

    render() {

        return (
            <FilterDivisionList title="Цвет" className="sidebar__color">
                {
                    CatalogConfig.filter.color.map((item) => <li className={this.state.active === item.name.toLowerCase() ? "filter-item-active" : ""} ><a onClick={() => {this.onChangeActive(item.name.toLowerCase())}} ><div className="color" style={{backgroundColor:item.value}} /><span className="color-name">{item.name}</span></a></li>)
                }
            </FilterDivisionList>
        );
    }
}



