import React, { Component } from 'react';
import { BubbleLoader } from 'react-css-loaders';

import OrderProductList from './OrderProductList/OrderProductList';
import OrderFormInfo from './OrderFormInfo/OrderFormInfo';

import LocalStorageHelper from '../../../../Classes/LocalStorageHelper';
import CartHelper from '../../../../Classes/CartHelper';

export default class OrderRegistration extends Component {

    priceListener = {value:0};

    constructor(props) {
        super(props);

        this.orderRegistration = this.orderRegistration.bind(this);
        this.orderChangeStatus = this.orderChangeStatus.bind(this);
    }

    orderRegistration(orderInfo) {

        if(orderInfo) {
            orderInfo["cart"] = LocalStorageHelper.getCart();
            this.orderChangeStatus(null, null, true);
            CartHelper.sendOrder(orderInfo, this.orderChangeStatus);
        }
    }

    orderChangeStatus(error, orderInfo, sending) {

        if(orderInfo && !error)
            orderInfo["price"] = this.priceListener.value;

        this.props.orderChangeStatus(error, orderInfo, sending);
    }

    render () {

        return (
            <div className="wrapper order-wrapper">
                <div className="site-path">
                    <ul className="site-path__items">
                        <li className="site-path__item"><a href="index.html">Главная</a></li>
                        <li className="site-path__item"><a href="#">Корзина</a></li>
                        <li className="site-path__item"><a href="#">Оформление заказа</a></li>
                    </ul>
                </div>
                <section className="order-process">
                    <h2 className="order-process__title">Оформление заказа</h2>
                    <OrderProductList priceListener={this.priceListener} />
                    <OrderFormInfo orderRegistration={this.orderRegistration} />
                </section>
            </div>
        );
    }
}




