import React, { Component } from 'react';
import PropTypes from 'prop-types';
import $ from 'jquery';

import SearchHelper from '../../../../Classes/SearchHelper';

export default class HeaderHiddenSearch extends Component {


    constructor(props) {
        super(props);

        this.state = {
            searchText: SearchHelper.getSearchText()
        };

        this.onChangeText = this.onChangeText.bind(this);
    }

    componentDidMount() {
        $('#searchInput').focus();
    }

    componentWillUnmount() {
        SearchHelper.deleteSearch();
    }

    componentWillReceiveProps(newProps) {
        if(!newProps.active) {
            this.setState({
                searchText: ""
            })
        }
    }

    onChangeText(event) {

        const newSearchText = event.currentTarget.value;

        this.setState({
            searchText:newSearchText
        });

        SearchHelper.searchTextUpdate(newSearchText);

        if(window.location.href.indexOf("catalog") === -1) {
            window.history.pushState({searchText: newSearchText}, 'Search', '/catalog');
            window.location.reload();
        }
    }

    render() {
        return (
            <form className={`header-main__search ${this.props.active ? "header-main__search_active" : ""}`} >
                <input placeholder="Поиск" id="searchInput" defaultValue={this.state.searchText} onChange={this.onChangeText} /><i className="fa fa-search" aria-hidden="true"/>
            </form>
        );
    }
}

HeaderHiddenSearch.PropTypes = {
    active:PropTypes.bool
};


