import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class Pagination extends Component {

    constructor(props) {
        super(props);

        this.state = {
            firstVisibleItem:this.props.activePage
        };
    }

    componentWillReceiveProps(newProps) {

        this.setState({
            firstVisibleItem: newProps.activePage
        }); 
    }

    getPages() {

        const countPages = this.props.countPages;

        let pages = [];

        if(countPages <= this.props.countVisiblePages) {
            for(let i = 1; i <= countPages; i++) {
                pages.push(this.getPage(i));
            } 
        } else {

            let indexPage = ((countPages - this.state.firstVisibleItem) < this.props.countVisiblePages) ? countPages - this.props.countVisiblePages : this.state.firstVisibleItem;

            for(let i = 1; i <= 5; i++) {
                pages.push(this.getPage(indexPage++));
            }
            
            if(indexPage === countPages - 1) {
                pages.push(this.getPage(indexPage));
            } else {
                pages.push(this.getPage(<li><a>...</a></li>));
            }

            pages.push(this.getPage(countPages));
        }

        return pages;
    }

    getPage(page) {
        return (
            <li className={page === this.props.activePage ? "active" : ""} key={page} >
                <a onClick={() => {this.props.onChangePage(page)}}>{page}</a>
            </li>
        );

    }

    onClickArrow (isNextArrow) {
            debugger;
        let firstVisibleItem = this.state.firstVisibleItem + (isNextArrow ? 1 : -1);

        if(firstVisibleItem > 0 && firstVisibleItem <= this.props.countPages) {

            this.setState({
                firstVisibleItem
            })
        }
    }

    render() {

        if(!this.props.countPages || this.props.countPages < 2) 
            return null;

        return (
            <div className="product-catalogue__pagination">
                <div className="page-nav-wrapper">
                    {(this.props.countPages > 7) && <div className="angle-back"><a onClick={() => {this.onClickArrow(false)}}/></div>}
                    <ul>
                        {
                            this.getPages()
                        }
                    </ul>
                    {(this.props.countPages > 7) && <div className="angle-forward"><a onClick={() => {this.onClickArrow(true)}}/></div>}
                </div>
            </div>
        );
    }
}

Pagination.PropTypes = {
    activePage:PropTypes.number.isRequired,
    countPages:PropTypes.number.isRequired,
    onChangePage:PropTypes.func.isRequired,
    countVisiblePages:PropTypes.number
};


