import React, { Component } from 'react';
import PropTypes from 'prop-types';

import HeaderPhone from "./HeaderPhone";
import HeaderLogo from "./HeaderLogo";

import HeaderButtons from "./HeaderButtons";

import HeaderMainHiddenBlocks from "./HeaderMainHeddenBlocks/HeaderMainHiddenBlocks";

export default class HeaderMain extends Component {

    constructor(props) {
        super(props);

        this.state = {
            activeBlock:null
        };

        this.onChangeActiveBlock = this.onChangeActiveBlock.bind(this);
    }

    onChangeActiveBlock(block) {

        if(this.state.activeBlock === block) {
            block = null
        }

        this.setState({
            activeBlock:block
        })
    }

    render() {
        return (
            <div className="header-main">
                <div className="header-main__wrapper wrapper">
                    <HeaderPhone {...this.props.headerPhone}/>
                    <HeaderLogo {...this.props.headerLogo} />
                    <HeaderButtons onChangeActiveBlock={this.onChangeActiveBlock} activeBlock={this.state.activeBlock} />
                </div>
                <HeaderMainHiddenBlocks activeBlock={this.state.activeBlock} onChangeActiveBlock={this.onChangeActiveBlock} />
            </div>
        );
    }
}

HeaderMain.propTypes = {
    headerPhone:PropTypes.object.isRequired,
    headerLogo:PropTypes.object.isRequired
};


