import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import PropTypes from 'prop-types';

import CartItem from './CartItem';

import CartHelper from '../../../../../../Classes/CartHelper';

export default class Cart extends Component {

    constructor(props) {
        super(props);

        this.state = {
            products:null,
            load:null,
            error:null
        };

        this.setProducts = this.setProducts.bind(this);
        this.onDeleteItem = this.onDeleteItem.bind(this);
        this.updateCart = this.updateCart.bind(this);
        this.closeCart = this.closeCart.bind(this);

        CartHelper.setListener("cart", this.updateCart);
    }

    componentDidMount() {
        this.updateCart();
    }

    componentWillUnmount() {
        CartHelper.deleteListener("cart");
    }

    setProducts(error, data) {

        if(!error) {

            this.setState({
                products:data,
                load:null,
                error:null
            })

        } else {

            this.setState({
                load:null,
                error:error
            })
        }
    }

    updateCart() {

        CartHelper.getItems(this.setProducts);

        this.setState({
            load:true
        })
    }

    onDeleteItem(id, size) {

        CartHelper.deleteProductFromCart(id, size);

        let products = this.state.products;

        for(let i = 0; i < products.length; i++) {

            let product = products[i];

            if(parseInt(product.id) === parseInt(id) && parseInt(product.size) === parseInt(size)) {
                products.splice(i, 1);
                break;
            }
        }

        this.setState({products});
    }

    closeCart() {
        if(this.props.onChangeActiveBlock)
            this.props.onChangeActiveBlock(null);
    }

    render() {

        if(this.state.load) {

            return (
                <div className={`${this.props.active ? "hidden-panel__basket_visible" : "hidden-panel__basket"} basket-dropped`} >
                    <div className="basket-dropped__title">Проверяем корзину...</div>
                </div>
            );
        }

        if(this.state.error) {

            return (
                <div className={`${this.props.active ? "hidden-panel__basket_visible" : "hidden-panel__basket"} basket-dropped`} >
                    <div className="basket-dropped__title">Ошибочка</div>
                </div>
            );
        }

        if(!this.state.products || this.state.products.length === 0) {

            return (
                <div className={`${this.props.active ? "hidden-panel__basket_visible" : "hidden-panel__basket"} basket-dropped`} >
                    <div className="basket-dropped__title">Упс, корзина пустая</div>
                </div>
            );
        }

        return (
            <div className={`${this.props.active ? "hidden-panel__basket_visible" : "hidden-panel__basket"} basket-dropped`} >
                <div className="basket-dropped__title">В вашей корзине:</div>
                <div className="basket-dropped__product-list product-list">
                    {
                        this.state.products.map(item => <CartItem {...item} onDeleteItem={this.onDeleteItem} />)
                    }
                </div><Link to="/order" className="basket-dropped__order-button" onClick={this.closeCart} >Оформить заказ</Link>
            </div>
        );
    }
}

Cart.PropTypes = {
    active:PropTypes.bool
};


