import React, { Component } from 'react';

import FilterDivisionList from './FilterDivisionList';
import FilterAbstractCheckboxList from './FilterAbstractCheckboxList';

import CatalogConfig from '../../../../Config/CatalogConfig';

export default class FilterSize extends FilterAbstractCheckboxList {

    key = "size";

    constructor(props) {
        super(props);
    }

    getItems() {

        let rows = [];

        const items = CatalogConfig.filter.sizes;

        for(let i = 0; i < items.length; i += 2) {

            let row = (
                <div className="sidebar__division__checkbox-list">
                    <li onClick={() => {this.onClickCheckbox(items[i])}} ><label><input type="checkbox" className="checkbox" defaultChecked={this.state.active.includes(items[i])}  name="sizes" value={items[i]}/><span className="checkbox-custom"/> <span className="label">{items[i]}</span></label></li>
                    {i + 1 <= items.length && <li onClick={() => {this.onClickCheckbox(items[i + 1])}} ><label><input type="checkbox" defaultChecked={this.state.active.includes(items[i + 1])}  className="checkbox" name="sizes" value={items[i + 1]}/><span className="checkbox-custom"/> <span className="label">{items[i + 1]}</span></label></li>}
                </div>
            );

            rows.push(row);
        }

        return rows;
    }

    render() {

        return (
            <FilterDivisionList title="Размер" className="sidebar__size">
                {
                    this.getItems()
                }
            </FilterDivisionList>
        );
    }
}



