import React, { Component } from 'react';
import PropTypes from 'prop-types';

import HiddenBlockProfileAndFavorite from './HiddenBlockProfileAndFavorite';
import Cart from './Cart/Cart';

export default class HeaderMainHiddenBlocks extends Component {

    render() {
        return (
            <div className={`header-main__hidden-panel hidden-panel ${ this.props.activeBlock ? "header-main__hidden-panel_visible" : "" }`} >
                <HiddenBlockProfileAndFavorite active={ this.props.activeBlock === "profile"}/>
                <Cart active={ this.props.activeBlock === "basket"} onChangeActiveBlock={this.props.onChangeActiveBlock} />
            </div>
        );
    }
}

HeaderMainHiddenBlocks.PropTypes = {
    activeBlock:PropTypes.string,
    onChangeActiveBlock:PropTypes.func
};


