import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';

import Favorite from './Favorite';

import SliderHelper from '../../Classes/SliderHelper';

export default class Product extends Component {

    constructor(props) {
        super(props);

        this.state = {
            images:this.props.images
        };
    }

    onClickArrow(isNextArrow, event) {

        debugger;

        let newPositionItems = SliderHelper.calcItems(0, !isNextArrow, this.state.images.length);

        let images = [];

        newPositionItems.forEach((item) => {
            images.push(this.state.images[item]);
        });

        this.setState({
            images
        });

        event.preventDefault();
    }

    render() {
        return (
            <Link to={`/product/${this.props.id}`} className="item-list__item-card item">
                <div className="item-pic"><img src={this.state.images[0]} alt="Босоножки женские"/>
                    <Favorite id={this.props.id} toggleFavorite={this.props.toggleFavorite}/>
                    {(this.state.images.length > 1) && <div className="arrow arrow_left" onClick={(event) => {this.onClickArrow(false, event)}}/>}
                    {(this.state.images.length > 1) && <div className="arrow arrow_right" onClick={(event) => {this.onClickArrow(true, event)}}/>}
                </div>
                <div className="item-desc">
                    <h4 className="item-name">{this.props.title}</h4>
                    <p className="item-producer">Производитель: <span className="producer">{this.props.brand}</span></p>
                    <p className="item-price">{this.props.price}</p>
                </div>
            </Link>
        );
    }
}

Product.PropTypes = {
    id:PropTypes.string.isRequired,
    images:PropTypes.arrayOf(PropTypes.string).isRequired,
    title:PropTypes.string.isRequired,
    brand:PropTypes.string.isRequired,
    price:PropTypes.string.isRequired
};


