import axios from 'axios';

import shopConfig from '../Config/ShopConfig';
import LocalStorageHelper from './LocalStorageHelper';
import GetData from './GetData';

export default class CartHelper {

    static listenersAction = [];

    static setListener(key, listener) {
        let newListener = {key, listener};
        this.listenersAction.push(newListener);
    }

    static deleteListener(key) {
        for(let i = 0; i < this.listenersAction.length; i++) {
            if(this.listenersAction.key === key) {
                this.listenersAction.splice(i, 1);
            }
        }
    }

    static updateCart () {
        this.listenersAction.forEach((item) => {
            if(typeof item.listener === "function") {
                item.listener();
            }
        })
    }

    static addProductInCart(id, size, amount) {

        let promise = new Promise((resolve, reject) => {

            const idCart = LocalStorageHelper.getCart();

            this.checkCartId(idCart)
                .then((idCartValid) => {

                    const product = {id, size, amount};

                    const url = `${shopConfig.apiHost}/cart${(idCart && idCartValid) ? `/${idCart}` : ''}`;

                    axios
                        .post(url, product)
                        .then(function (response) {
                            LocalStorageHelper.setCart(response.data.data.id);
                            CartHelper.updateCart();
                            resolve();
                        })
                        .catch(function (error) {
                            CartHelper.updateCart();
                            reject();
                        });

                })
        });

        return promise;
    }

    static deleteProductFromCart(id, size) {
        const amount = 0;
        return this.addProductInCart(id, size, amount)
    }

    static checkProduct(id, size, callback) {

        const idCart = LocalStorageHelper.getCart();

        if(!idCart) {
            callback(false, false);
            return
        }

        axios.get(`${shopConfig.apiHost}/cart/${idCart}`)
            .then(function (response) {

                let data = response.data.data && response.data.data.products;

                if(!data)
                    callback(false, false);

                for(let i = 0; i < data.length; i++) {

                    let item = data[i];

                    if (parseInt(item.id) === parseInt(id) && parseInt(item.size) === parseInt(size)) {
                        callback(false, true);
                        return;
                    }
                }

                callback(false, false);
            })
            .catch(function (error) {debugger;

                if(error.response.status === 404) {
                    callback(false, false);
                } else {
                    callback(error);
                }
            });
    }

    static getItems(callback) {

        const idCart = LocalStorageHelper.getCart();

        if(!idCart) {
            callback(false, null);
            return;
        }

        let dataFromCart;

        const callbackForDetData = (error, data) => {

            if(error) {
                callback(error);
                return;
            }

            dataFromCart.forEach((itemCart) => {

                let fullDataItem = data.filter(item => item.id === itemCart.id);

                if(fullDataItem && fullDataItem.length !== 0) {
                    for(let prop in fullDataItem[0]) {
                        itemCart[prop] = fullDataItem[0][prop];
                    }
                }
            });
                debugger;
            callback(false, dataFromCart);
        };

        axios.get(`${shopConfig.apiHost}/cart/${idCart}`)
            .then(function (response) {

                let data = response.data.data && response.data.data.products;

                dataFromCart = data;

                if(!data || data.length === 0) {
                    callback(false, null);
                    return;
                }

                let idItems = data.map(item => item.id);

                GetData.getProductsById(idItems, callbackForDetData)

            })
            .catch(function (error) {
                if(error.response.status === 404) {
                    callback(false, false);
                } else {
                    callback(error);
                }
            });

    }

    static sendOrder(orderInfo, callback) {

        this.checkCartId(orderInfo.cart)
            .then((validCart) => {

            if(validCart) {

                axios
                    .post(`${shopConfig.apiHost}/order`, orderInfo)
                    .then(function () {debugger;
                        LocalStorageHelper.deleteCart();
                        CartHelper.updateCart();
                        callback(false, orderInfo);
                    })
                    .catch(function (error) {
                        CartHelper.updateCart();
                        callback(error, orderInfo);
                        debugger;
                    });

            } else {debugger;

                let error = {
                    message:"Упс, извините, мы потеряли вашу корзину",
                    status: 404
                };

                callback(error, orderInfo)
            }

            })

    }

    static checkCartId(id) {

        let promise = new Promise((resolve) => {

            axios.get(`${shopConfig.apiHost}/cart/${id}`)
                .then(() => {
                    resolve(true);
                })
                .catch(() => {
                    resolve(false);
                });
        });

        return promise;
    }
}


