import React, { Component } from 'react';
import { BubbleLoader } from 'react-css-loaders';

import FavoriteItems from './FavoriteItems';
import Sorting from '../../Shared/Sorting';

import GetData from '../../../Classes/GetData';
import LocalStorageHelper from '../../../Classes/LocalStorageHelper';

export default class FavoritePage extends Component {

    constructor(props) {
        super(props);

        this.state = {
            products:null,
            error:false,
            load:false
        };

        this.setProducts = this.setProducts.bind(this);
        this.unFavorite = this.unFavorite.bind(this);
        this.onChangeSorting = this.onChangeSorting.bind(this);
    }

    componentDidMount() {

        let itemsId = LocalStorageHelper.getFavorite();

        if(itemsId && itemsId.length !== 0) {
            GetData.getProductsById(itemsId, this.setProducts);

            this.setState({
                load:true
            });
        }
    }

    setProducts(error, data) {
        if(!error) {
            this.setState({
                products:data,
                load:false
            });
        }
        else {
            this.setState({
                error
            });
        }
    }

    unFavorite(id) {
        let products = this.state.products;

        for(let i = 0; i < products.length; i++) {
            if(parseInt(products[i].id) === parseInt(id)) {
                products.splice(i, 1);
                break;
            }
        }

        this.setState({
            products
        })
    }

    onChangeSorting(value) {

        let products = this.state.products;

        if(!products || products.length === 0)
            return;

        const sortByPrice = (a, b) =>{
            if (a.price > b.price) return 1;
            if (a.price < b.price) return -1;
        };

        const sortByPopularity = (a, b) => {
            if (a.id > b.id) return 1;
            if (a.id < b.id) return -1;
        };

        if(value === "price") {
            products = products.sort(sortByPrice);
        }

        if(value === "popularity") {
            products = products.sort(sortByPopularity);
        }

        this.setState({
            products
        });
    }

    render () {

        if(this.state.load) {

            return(
                <div className="wrapper wrapper_favorite">
                    <main className="product-catalogue">
                        <section className="loader">
                            <BubbleLoader/>
                        </section>
                    </main>
                </div>
            );
        }

        if(this.state.error) {

            return(
                <div className="wrapper wrapper_favorite">
                    <main className="product-catalogue">
                        <section className="loader">
                            Ошибочка
                        </section>
                    </main>
                </div>
            );
        }

        return (
            <div className="wrapper wrapper_favorite">
                <main className="product-catalogue product-catalogue_favorite">
                    <section className="product-catalogue__head product-catalogue__head_favorite">
                        <div className="product-catalogue__section-title">
                            <h2 className="section-name">В вашем избранном</h2><span className="amount amount_favorite"> {this.state.products ? this.state.products.length : 0} товаров</span>
                        </div>
                        <Sorting value={this.state.sortBy} onChangeSorting={this.onChangeSorting}/>
                    </section>
                    <FavoriteItems products={this.state.products} toggleFavorite={this.unFavorite} />
                </main>
            </div>
        );
    }
}




