import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class FilterDiscount extends Component {

    key = "discounted";

    constructor(props) {
        super(props);

        this.state = {
            active: this.props.filter[this.key] || false
        };

        this.onClickCheckbox = this.onClickCheckbox.bind(this);
    }

    componentWillReceiveProps(newProps) {

        this.setState({
            active:newProps.filter[this.key]
        })
    }

    onClickCheckbox() {

        let filter = {
            key:this.key,
            value:!this.state.active
        };

        this.props.onChangeFilter([filter]);
    }

    render() {

        return (
            <label onClick={this.onClickCheckbox}><input type="checkbox" className="checkbox" name="checkbox-disc"/><span className="checkbox-discount"/> <span className="text-discount">Со скидкой</span></label>
        );
    }
}

FilterDiscount.PropTypes = {
    filter:PropTypes.object.isRequired,
    onChangeFilter:PropTypes.func.isRequired
};



