import React, { Component } from 'react';
import { BubbleLoader } from 'react-css-loaders';

import OrderProductItem from './OrderProductItem';

import CartHelper from "../../../../../Classes/CartHelper";

export default class OrderProductList extends Component {

    constructor(props) {
        super(props);

        this.state = {
            products:null,
            load:null,
            error:null
        };

        this.setProducts = this.setProducts.bind(this);
        this.onChangeCount = this.onChangeCount.bind(this);
    }

    componentDidMount() {

        CartHelper.getItems(this.setProducts);

        this.setState({
            load:true
        })
    }

    componentWillUpdate() {
        debugger;
    }

    setProducts(error, data) {

        if(!error) {

            this.setState({
                products:data,
                load:null,
                error:null
            })

        } else {

            this.setState({
                load:null,
                error:error
            })
        }
    }

    onChangeCount (id, size, amount) {

        let products = this.state.products;

        CartHelper.addProductInCart(id, size, amount);

        for(let i = 0; i < products.length; i++) {

            let product = products[i];

            if(parseInt(product.id) === parseInt(id) && parseInt(product.size) === parseInt(size)) {
                product.amount = amount;
                break;
            }
        }

        this.setState({
            products
            });
    }

    getAmountCart() {
        let amount = 0;

        this.state.products.forEach((item) => {
            amount += item.price * item.amount;
        });

        if(this.props.priceListener)
            this.props.priceListener.value = amount;

        return amount;
    }

    render () {

        if(this.state.load) {

            return (
                <div className="order-process__basket order-basket">
                    <div className="loader">
                        <BubbleLoader/>
                    </div>
                </div>
            );
        }

        if(this.state.error) {

            return (
                <div className="order-process__basket order-basket">
                    <div className="order-basket__title">Ошибка</div>
                </div>
            );
        }

        if(!this.state.products || this.state.products.length === 0) {

            return (
                <div className="order-process__basket order-basket">
                    <div className="order-basket__title">Ваша корзина пустая</div>
                </div>
            );
        }

        return (
            <div className="order-process__basket order-basket">
                <div className="order-basket__title">в вашей корзине:</div>
                <div className="order-basket__item-list">
                    {
                        this.state.products.map(item => <OrderProductItem {...item} onChangeCount={this.onChangeCount} />)
                    }
                </div>
                <div className="order-basket__summ">Итого:<span>{this.getAmountCart()} <i className="fa fa-rub" aria-hidden="true"/></span></div>
            </div>
        );
    }
}




