import React, { Component } from 'react';
import PropTyps from 'prop-types';


export default class OrderRadioGroup extends Component {

    render () {

        return (
            <div className="order-process__paid">
                <h3 className="h3">{this.props.title}</h3>
                <div className="order-process__paid-form">

                    {
                        this.props.items.map((item) => {
                            return (
                                <label className="order-process__paid-label">
                                    <input className="order-process__paid-radio" required type="radio" name={this.props.name} value={item.value}/><span className="order-process__paid-text">{item.name}</span>
                                </label>
                            );
                        })
                    }

                </div>
            </div>
        );
    }
}

OrderRadioGroup.PropTypes = {
    title:PropTyps.string.isRequired,
    name:PropTyps.string.isRequired,
    items:PropTyps.arrayOf(PropTyps.shape({
        name:PropTyps.string.isRequired,
        value:PropTyps.string.isRequired
    }))
};


