import React, { Component } from 'react';
import PropTypes from 'prop-types';

import CatalogConfig from '../../Config/CatalogConfig';


export default class Sorting extends Component {

    constructor(props) {
        super(props);

        this.state = {
            value:this.props.value || CatalogConfig.sorting[0].value
        }
    }

    componentWillReceiveProps (newProps) {
        this.setState({
            value:newProps.value || CatalogConfig.sorting[0].value
        })
    }

    onChange (event) {

        let value = event.currentTarget.value;
        
        if(this.props.onChangeSorting) 
            this.props.onChangeSorting(value);
    }

    render() {

        return (
            <div className="product-catalogue__sort-by">
                <p className="sort-by">Сортировать</p>
                <select name="" id="sorting" onChange={this.onChange.bind(this)} defaultValue={this.state.value} >
                    {
                        CatalogConfig.sorting.map((item) => {
                            return (
                                <option value={item.value} key={item.value}>{item.name}</option>
                            );
                        })
                    }
                </select>
            </div>
        );
    }
}

Sorting.PropTypes = {
    value:PropTypes.string,
    onChangeSorting:PropTypes.func.isRequired
};


