import React, { Component } from 'react';
import PropTypes from 'prop-types';
import $ from 'jquery';
import 'jqueryui';

import '../../../../css/jquery-ui.css';

import CatalogConfig from '../../../../Config/CatalogConfig';

export default class FilterPrice extends Component {

    constructor(props) {
        super(props);

        this.state = {
            minPrice: this.props.minPrice || CatalogConfig.defaultFilter.minPrice,
            maxPrice: this.props.maxPrice || CatalogConfig.defaultFilter.maxPrice,
            open:this.props.isDefaultOpen || true
        };

        this.onSlideSlider = this.onSlideSlider.bind(this);
        this.onChangeSlider = this.onChangeSlider.bind(this);

        this.toggleBlock = this.toggleBlock.bind(this);
    }

    componentDidMount() {
        $( "#priceSlider" ).slider({
            max: 60000,
            min: 1000,
            range: true,
            step: 5,
            values:[this.state.minPrice, this.state.maxPrice],
            slide:this.onSlideSlider,
            change:this.onChangeSlider
        });
    }

    componentWillReceiveProps(newProps) {
        let minPrice = newProps.minPrice || CatalogConfig.defaultFilter.minPrice;
        let maxPrice = newProps.minPrice || CatalogConfig.defaultFilter.maxPrice;

        $( "#priceSlider" ).slider({
            values:[minPrice, maxPrice]
        });

        this.setState({
            minPrice,
            maxPrice
        });
    }

    componentWillUnmount() {
        $( "#priceSlider" ).slider("destroy");
    }

    onSlideSlider () {
        let values = $( "#priceSlider" ).slider( "option", "values" );

        this.setState({
            minPrice: values[0],
            maxPrice: values[1]
        })
    }

    onChangeSlider () {

        let filter = [];

        if(this.props.filter.minPrice === this.state.minPrice && this.props.filter.maxPrice === this.state.maxPrice)
            return;

        filter.push({
            key:"minPrice",
            value:this.state.minPrice
        });

        filter.push({
            key:"maxPrice",
            value:this.state.maxPrice
        });

        this.props.onChangeFilter(filter);
    }

    toggleBlock() {

        if(this.state.open) {
            $('.price-slider__wrapper').hide(400);
        } else {
            $('.price-slider__wrapper').show(400);
        }

        this.setState({
            open:!this.state.open
        });
    }


    render() {

        return (
            <section className="sidebar__division">
                <div className="sidebar__price">
                    <div className="sidebar__division-title" onClick={this.toggleBlock} >
                        <h3>Цена</h3><div className={this.state.open ? "opener-down" : "opener-up"}/>
                    </div>
                    <div className="price-slider__wrapper">
                        <div id="priceSlider" className="price-slider"/>
                        <div className="counter">
                            <input type="text" className="input-1" value={this.state.minPrice}/>
                            <div className="input-separator"/>
                            <input type="text" className="input-2" value={this.state.maxPrice}/>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

FilterPrice.PropTypes = {
    filter:PropTypes.object.isRequired,
    onChangeFilter:PropTypes.func.isRequired
};



