import React, { Component } from 'react';
import { BubbleLoader } from 'react-css-loaders';

import Filter from './Filter/Filter';
import Catalog from './Catalog';
import Pagination from '../../Shared/Pagination';
import OverlokedSlider from '../CardProductPage/OverlookedSlider';
import Sorting from '../../Shared/Sorting';

import GetData from '../../../Classes/GetData';
import CatalogConfig from '../../../Config/CatalogConfig';
import SearchHelper from '../../../Classes/SearchHelper';

export default class CatalogPage extends Component {

    constructor(props) {
        super(props);

        this.state = {
            products:null,
            page:1,
            pages:null,
            amount:null,
            filter:this.getFilterFromUrlParams(),
            sortBy:null,
            search:SearchHelper.getSearchText(),
            error:false,
            load:false
        };

        this.setProducts = this.setProducts.bind(this);
        this.onChangePage = this.onChangePage.bind(this);
        this.onChangeSorting = this.onChangeSorting.bind(this);
        this.onChangeFilter = this.onChangeFilter.bind(this);
        this.onChangeSearchText = this.onChangeSearchText.bind(this);

        this.getFilterFromUrlParams = this.getFilterFromUrlParams.bind(this);
        this.getNewFilter = this.getNewFilter.bind(this);

        SearchHelper.setListener("catalog", this.onChangeSearchText);
    }

    componentWillReceiveProps(newProps) {
        let filter = this.getFilterFromUrlParams(newProps.match && newProps.match.params);

        GetData.getProductsWithFilter(filter , this.setProducts);

        this.setState({
            products:null,
            page:1,
            pages:null,
            goods:null,
            filter,
            sortBy:null,
            load:true
        })
    }

    componentDidMount() {

        GetData.getProductsWithFilter(this.state.filter , this.setProducts);

        this.setState({
            load:true
        });
    }

    componentWillUnmount() {
        SearchHelper.deleteListener("catalog");
    }

    getFilterFromUrlParams(urlParams) {

        urlParams = urlParams || this.props.match && this.props.match.params;

        let filter = CatalogConfig.defaultFilter;

        if(urlParams) {

            if(urlParams.categoryId && parseInt(urlParams.categoryId) !== 0) {
                filter["categoryId"] = parseInt(urlParams.categoryId);
            }

            if(urlParams.filterKey && urlParams.filterValue) {
                if(CatalogConfig.filter[urlParams.filterKey]) {
                    filter[urlParams.filterKey] = urlParams.filterValue;
                }
            }
        }

        return filter;
    }

    setProducts(error, response) {
        if(!error) {debugger;
            this.setState({
                products:response.data.data,
                page:response.data.page,
                pages:response.data.pages,
                goods:response.data.goods,
                load:false
            });
        }
        else {
            this.setState({
                error,
                load:false
            });
        }
    }

    onChangePage(page) {

        if(page && parseInt(page)) {
            
            page = parseInt(page);

            if(page > 0 && page <= this.state.pages) {

                let filter = this.getNewFilter(null, page, null);

                GetData.getProductsWithFilter(filter , this.setProducts);

                this.setState({
                    load:true
                });
            }
        }
    }

    onChangeSorting(value) {

        if(!value)
            value = "";
            
        let filter = this.getNewFilter(null, null, value);

        GetData.getProductsWithFilter(filter , this.setProducts);

        this.setState({
            load:true,
            sortBy:value
        });
    }

    onChangeFilter(filter) {

        let filterForRequest = this.getNewFilter(filter, null, null);

        this.setState({
            load:true,
            filter
        });

        GetData.getProductsWithFilter(filterForRequest , this.setProducts);
    }

    onChangeSearchText(search) {
        debugger;
        let filter = this.getNewFilter(null, null, null, search);

        this.setState({
            load:true,
            search
        });

        GetData.getProductsWithFilter(filter , this.setProducts);
    }

    getNewFilter(filter, page, sortBy, search) {

        filter = filter || this.state.filter || {};

        let filterForRequest = {};

        for (let key in filter) {
            filterForRequest[key] = filter[key];
        }

        page = page || this.state.page;
        sortBy = sortBy || this.state.sortBy;
        search = search || this.state.search;

        if(page)
            filterForRequest["page"] = page;

        if(sortBy)
            filterForRequest["sortBy"] = sortBy;

        if(search) {
            filterForRequest["title"] = search;
        }
            
        return filterForRequest;
    }

    getCatalogContent() {

        if(this.state.load) {
            return (
                <section className="loader">
                    <BubbleLoader/>
                </section>
            );
        }

        if(this.state.error) {

            return (
                <section className="loader">
                    Ошибочка
                </section>
            );
        }

        return (
            <section className="product-catalogue-content">

                <section className="product-catalogue__head">
                    <div className="product-catalogue__section-title">
                        <h2 className="section-name">Женская обувь</h2><span className="amount"> {`${this.state.goods ? `${this.state.goods} ед. товара` : ""}`}</span>
                    </div>
                    <Sorting value={this.state.sortBy} onChangeSorting={this.onChangeSorting}/>
                </section>
                <Catalog products={this.state.products} />
                <Pagination activePage={this.state.page} countPages={this.state.pages} onChangePage={this.onChangePage} countVisiblePages={7}/>
            </section>
        );

    }

    render() {

        return (
            <main className="product-catalogue">

                <Filter filter={this.state.filter} onChangeFilter={this.onChangeFilter}/>
                {
                    this.getCatalogContent()
                }
                <OverlokedSlider countVisibleImage="5"/>
            </main>
        )
    }
}



