export default class SearchHelper {

    static listenersAction = [];
    static searchText;

    static searchTextUpdate (text) {

        this.searchText = text;

        if(this.listenersAction && this.listenersAction.length !== 0) {

            this.listenersAction.forEach((item) => {
                if(typeof item.listener === "function") {
                    item.listener(this.searchText);
                }
            })
        }
    }

    static setListener(key, listener) {
        let newListener = {key, listener};
        this.listenersAction.push(newListener);
    }

    static deleteListener(key) {
        for(let i = 0; i < this.listenersAction.length; i++) {
            if(this.listenersAction.key === key) {
                this.listenersAction.splice(i, 1);
            }
        }
    }

    static getSearchText () {

        if(this.searchText)
            return this.searchText;

        let stateHistory = window.history.state;

        if(stateHistory) {
            this.searchText = stateHistory.searchText;
            return this.searchText;
        }

        return "";
    }

    static deleteSearch() {
        window.history.pushState({searchText: null}, 'Search', '/catalog');
        window.history.forward();
        this.searchText = null;

        this.searchTextUpdate(null);
    }
}



