import React, { Component } from 'react';
import PropTypes from 'prop-types';

import LocalStorageHelper from '../../Classes/LocalStorageHelper';

export default class Favorite extends Component {

    constructor(props) {
        super(props);

        this.state = {
            favorite:this.checkFavorite()
        }
    }

    componentWillUpdate(newProps) {

        if(newProps.id !== this.props.id) {
            this.setState({
                favorite:this.checkFavorite()
            })
        }
    }

    checkFavorite() {
        const favorites = this.props.favorites || LocalStorageHelper.getFavorite();

        if(favorites)
            return favorites.includes(this.props.id);

        return false;
    }

    toggleFavorite(isFavorite, event) {

        if(isFavorite) {
            LocalStorageHelper.removeFavorite(this.props.id);

            this.setState({
                favorite:false
            });
        }
        else {
            LocalStorageHelper.setFavorite(this.props.id);

            this.setState({
                favorite:true
            });
        }
        debugger;
        if(this.props.toggleFavorite)
            this.props.toggleFavorite(this.props.id);

        event.preventDefault();
    }

    render() {

        if(this.state.favorite)
        {
            return (
                <div className="new-deals__product_favorite chosen" onClick={(event) => {this.toggleFavorite(true, event);}} />
            );
        }
        else
        {
            return (
                <div className="new-deals__product_favorite" onClick={(event) => {this.toggleFavorite(false, event);}} />
            );
        }
    }
}

Favorite.propTypes = {
    id:PropTypes.number.isRequired,
    favorites:PropTypes.arrayOf(PropTypes.number),
    toggleFavorite:PropTypes.func
};



